.leaflet-container {
  width: 100%;
  height: 65vh;
  z-index: 1;
}

.top-container {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 35vh;
}

.top-image {
  width: 100%;
  height: 100%;
}

.display {
  display: flex;
  position: absolute;
  top: 200px;
  z-index: 2;
  background-color: white;
  width: 75%;
  height: 180px;
  border-radius: 20px;
}

.top-form {
  display: flex;
  position: absolute;
  top: 110px;
  z-index: 2;
  width: 500px;
  height: 50px;
}

.input {
  display: block;
  width: 100%;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  font-size: larger;
}

.submit {
  display: block;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  background-color: black;
  color: white;
  border-style: none;
  font-size: larger;
  font-weight: bolder;
}

.title {
  display: block;
  color: white;
  position: absolute;
  top: 20px;
}

.info-title {
  color: grey;
  padding-bottom: 2px;
}

.info-data {
  color: black;
}

.data {
  border-left: 2px;
  padding: 30px;
  width: 25%;
}
